* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

.container {
  width: 80%;
  margin: 0 auto;
  padding: 20px;
}

h1, h2 {
  margin-bottom: 15px;
}

p {
  margin-bottom: 10px;
}

/* CTA Section */
/* CTA Section */
.cta h1 {
  font-size: 2.5em;
  margin-bottom: 20px;
}

.cta p {
  font-size: 1.2em;
  margin-bottom: 20px;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}


button {
  padding: 15px 30px;
  background-color: #099c8a;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 900;
}

 button:hover {
  background-color: #05665b;
}

.cta button {
  background-color: #EFC06A;
  color: white;
}

.cta button:hover {
  background-color: #e0b160;
}


/* Section Styling */
.section {
  display: flex;
  align-items: center;
  background-color: white;
  padding: 60px 0; /* Add more space around sections */
  color: #333; /* Dark grey for text */
  line-height: 1.8;
}

.section:nth-child(even) {
  background-color: #F7EDE2;
}

/* For even sections, reverse the order of text and image */
.section:nth-child(even) .text {
  order: 2;
}

.section:nth-child(even) .image {
  order: 1;
}


.section .container {
  display: flex;
  flex-direction: row;
}

.section .text {
  flex: 1;
  padding: 20px 60px;
}



.section h2 {
  font-size: 2em;
  margin-bottom: 20px; /* Add space between header and text */
  color: #222; /* Darker grey for headers */
}

.section p {
  font-weight: 400; /* Softer font weight for paragraph text */
  font-size: 1.1em;
  margin-top: 30px;
  color: #444; /* Slightly lighter grey for paragraphs */
}

.section .image {
  flex: 1;
}

.section .image img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}


.beige-background {
  background-color: #F7EDE2;
}

/* CTA Section */
.cta {
  background-color: #099c8a;
  color: white;
  padding: 50px 0;
  text-align: center;

  position: fixed; /* Fix the CTA section */
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh; /* Full viewport height */
  z-index: 1; /* Place it behind the main content */
}

/* Adjust the container inside CTA */
.cta .container {
  position: relative;
  top: 50%;
  transform: translateY(-50%); /* Vertically center the content */
}

/* Main Content */
.main-content {
  position: relative;
  z-index: 2; /* Ensure it appears above the CTA */
}

/* Add top margin to main content to prevent overlap */
.main-content {
  margin-top: 100vh; /* Push the content down by the height of the CTA */
}



/* Ensure the footer is above the CTA */
.footer {
  position: relative;
  z-index: 2;
  background-color: #333;
  color: white;
  padding: 20px 0;
  text-align: center;
  margin-top: auto;
}



.footer a {
  color: #EFC06A;  /* Set a desired color for the links */
  text-decoration: none;  /* Remove underline */
  margin: 0px;
}

.footer a:hover {
  text-decoration: underline;  /* Add underline on hover for clarity */
  color: #e0b160;  /* Optional: Change color on hover */
}

.footer-columns {
  text-align: left;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap; /* Allows columns to wrap on smaller screens */
}

.footer-column {
  flex: 1;
  max-width: 300px;
  margin: 10px;
}


.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #ccc;
  z-index: 100;
}

.navbar .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar .logo {
  font-size: 24px;
  color: #099c8a;
  text-decoration: none;
}

.navbar ul {
  list-style: none;
  display: flex;
}

.navbar li {
  margin-left: 20px;
}

.navbar a {
  text-decoration: none;
  color: #333;
  font-weight: bold;
}

.navbar a:hover {
  color: #099c8a;
}

.logo-desktop, .logo-mobile {
  max-width: 100%;
  height: auto;
}

.logo-mobile {
  display: none;
}

.logo img {
  max-width: 100%;
  height: auto; /* Maintains aspect ratio */
  max-height: 80px; /* Or adjust based on design */
}


/* Responsiveness */
@media (max-width: 768px) {
  .logo-desktop {
    display: none;
  }
  
  .logo-mobile {
    display: block;
  }

  .logo img {
    max-height: 60px; /* Adjust for mobile */
  }

  .section .container {
    flex-direction: column;
  }

  .section:nth-child(even) .text {
    order: 1;
  }
  
  .section:nth-child(even) .image {
    order: 2;
  }

  .section {
    padding: 40px 20px; /* Adjust padding for mobile */
  }

  .footer-columns {
    flex-direction: column;
  }

  .footer-column {
    margin: 10px 0;
  }
}

